@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR);@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic);@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR);@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic);
body{
  margin:0
}
.search{
  
  font-size:0;
  margin-left:32rem;
  background-color:transparent;
  background-image:url(data:image/svg+xml;base64,);
  border:0;
  width:1.5rem;
  height:1.5rem;
  padding-top:1rem;
  background-repeat:no-repeat;
  position:static;
}
a{
    text-decoration:none;
    color:black;
}
span{
  display:inline-block;
  margin-top:1rem;
  margin-left:11rem;
}
.topmenu a {
  font-family : 'Nanum Gothic',sans-serif;
  font-weight:bold;
}
#main{
  font-size:1.8rem;
}
.class a{
  font-size:1.3rem;
}
.class{
  display:inline;
  margin-left:4.5rem;
}
.class a {
  margin-right:3rem;
}
.topmenu{
  height:70px;
  display:block;
  background-color:#50dce4;
}

html{
    background-color:rgb(228, 228, 228);
}
table{
    margin-left:20rem;
    margin-top:3rem;
}
.title{
    color:rgb(255, 0, 0);
}
.post{
    background-color:white;
    display:inline-block;
    float:left;
    text-align:center;
    width:280px;
    height:450px;
    margin:2rem;
}
.box{
    font-family:'Noto Sans KR',sans-serif;
}
a{
    text-decoration:none;
    color:black;
}
.postlink{
    font-family: 'Noto Sans KR', sans-serif;
    text-align:center;
    font-size:1rem;
    padding:0.5rem;
    margin-top:5rem;
}

